div.header-div {
    //固定在上面
    position: fixed;
    top: 0;
    width: 100%;
    //背景顏色
    background-color: #4A90E2;

    //項目清單
    div.content-nav {
        width: 100%;

        nav {
            text-align: center;

            ul {
                margin: 0 0 -4px 0;
                padding: 0;
                //搭配 nav 的置中，可以讓 ul 的項目置中
                display: inline-block;

                li {
                    font-size: 24px;
                    float: left;
                    //隱藏 li 前面的點
                    display: inline;

                    a {
                        display: block;
                        color: #00408B;
                        text-align: center;
                        padding: 14px 16px;
                        //消除超連結底線
                        text-decoration: none;
                    }

                    //鼠標移過去時變更樣式
                    /*a:hover:not(.active) {
                        background-color: #ddd;
                    }*/

                    //設定被點選後要變化的樣式
                    a.active {
                        color: white;
                        border-bottom: solid 5px;
                        border-bottom-color: #00408B;
                    }
                }
            }
        }
    }

    //用於手機板的 bars
    div.bars-div {
        display: none;
    }
}