input#add-task {
    width: 97%;
    padding: 10px 5px;
    border: 2px solid #C8C8C8;
    background-color: #ffffff;
    border-radius: 5px;
    font-size: 20px;
}

input#add-task:hover {
    cursor: pointer;
}

div.add-task-div {
    width: 100%;
    background-color: #F2F2F2;
    box-shadow: 0 4px 4px 0 #C8C8C8;
    border-radius: 5px;
    height: 0;
    overflow: hidden;
    transition: 0.5s;

    div.add-task-header {
        padding: 14px 16px;
        height: 45px;
        border-bottom: 2px solid #C8C8C8;

        div.add-task-title {
            float: left;
            width: 80%;
            text-align: left;

            input.add-task-fin-box {
                width: 20px;
                height: 20px;
                margin: 9px 0;
            }

            input.add-task-text {
                width: 86%;
                padding: 8px 4px 4px 4px;
                margin-left: 10px;
                font-size: 16px;
                border: 2px solid #C8C8C8;
                background-color: #ffffff;
                border-radius: 5px;
            }

            span.add-task-span {
                font-size: 20px;
                padding-left: 10px;
                position: relative;
                bottom: 4px;
            }

            input.add-task-text:focus {
                border-color: #4A90E2 !important;
                outline: 0;
            }

            div.add-task-sm-ico {
                padding-left: 25px;
                position: relative;
                bottom: 6px;

                span {
                    color: #757575;
                    padding-right: 10px;
                }
            }
        }

        div.add-task-icon {
            float: right;
            width: 20%;
            text-align: right;

            span:first-child {
                padding-right: 8px;
            }

            span:first-child:hover {
                cursor: pointer;
            }

            span:first-child i.fas {
                color: #F5A623;
            }
            
            span.edit-pen:hover {
                cursor: pointer;
            }

            span.edit-pen i {
                color: #4A90E2;
            }
        }
    }

    div.add-task-content {
        text-align: left;
        padding: 10px 0 10px 50px;

        div {
            padding-bottom: 5px;
        }

        span.date-span {
            padding-right: 5px;
        }

        div.date-div {
            input.date-input {
                margin: 5px 0 0 18px;
            }


            input.date-input:invalid + span:after {
                content: '✖';
                padding-left: 5px;
            }

            input.date-input:valid + span:after {
                content: '✓';
                padding-left: 5px;
            }
        }

        div.file-div {
            button.file-btn {
                background-color: #C8C8C8;
                border-radius: 2px;
                border: 0;
                color: white;
                font-size: 20px;
                margin: 5px 0 0 18px;
            }

            button.file-btn:hover {
                cursor: pointer;
            }
        }

        div.comment-div {
            textarea.comment-textarea {
                width: 320px;
                border-radius: 2px;
                border: 0;
                margin: 5px 0 0 18px;
            }

            textarea.comment-textarea:focus {
                border: 1px solid #4A90E2;
                outline: 0;
            }

            /* 隱藏 scroll => chrome safari */
            textarea.comment-textarea::-webkit-scrollbar {
                display: none;
            }
        }
    }

    div.add-task-btn {
        button {
            width: 50%;
            height: 62px;
            border: 0;
            font-size: 25px;
        }

        button:hover {
            cursor: pointer;
        }

        button.add-btn {
            background-color: #4A90E2;
            color: white;
        }

        button.add-btn:hover {
            background-color: #3982D7;
        }

        button.cancel-btn {
            background-color: #FDFDFD;
            color: #D0021B;
        }

        button.cancel-btn:hover {
            background-color: #F7F7F7;
        }
    }
}