div.header-div {
    position: fixed;
    top: 0;
    width: 100%;
    height: 65px;
    background-color: #4A90E2;

    div.header-title {
        position: absolute;
        padding: 16px 0 0 10px;
        font-size: 24px;
        font-weight: bold;
    }

    div.content-nav {
        height: calc(100vh - 65px);
        background-color: #4A90E2;
        overflow: hidden;
        position: fixed;
        top: 65px;
        width: 0;
        transition: 0.3s;

        nav {
            ul {
                padding-left: 15px;

                li {
                    padding-bottom: 5px;
                    font-size: 24px;
                    display: block;

                    a {
                        color: #00408B;
                        text-align: left;
                        //消除超連結底線
                        text-decoration: none;
                    }

                    //設定被點選後要變化的樣式
                    a.active {
                        color: white;
                    }
                }
            }
        }
    }

    div.bars-div {
        float: right;

        .bars-btn {
            font-size: 30px;
            background-color: #4A90E2;
            border: 0;
            padding: 18px 15px 0 0;
        }
    }
}